'use client';

import Image from 'next/image';
import { Card } from '@/components/Card';
import { Progress } from '@/components/Progress';

export default function TeamGrid({ teamMembers }) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6 p-4">
      {teamMembers.map((member) => (
        <Card key={member.id} className="flex flex-col gap-4 p-6">
          <div className="flex items-center gap-4">
            <Image
              src={member.avatar}
              alt={member.name}
              width={64}
              height={64}
              className="rounded-full h-16 w-16 object-cover"
            />
            <div className="flex flex-1 flex-col justify-center">
              <p className="text-lg font-bold">{member.name}</p>
              <p className="text-sm text-muted-foreground">{member.role}</p>
            </div>
          </div>
          <div>
            <div className="flex justify-between items-center mb-1">
              <p className="text-sm text-muted-foreground">{member.tasks.assigned} tasks assigned</p>
              <p className="text-sm font-medium">
                {member.tasks.completed}/{member.tasks.total} Tasks
              </p>
            </div>
            <Progress
              value={(member.tasks.completed / member.tasks.total) * 100}
              color={
                (member.tasks.completed / member.tasks.total) * 100 > 80
                  ? 'red'
                  : (member.tasks.completed / member.tasks.total) * 100 > 60
                  ? 'orange'
                  : 'green'
              }
            />
          </div>
        </Card>
      ))}
    </div>
  );
}
